<template>
  <div>


    
    <card class="card" title="Procesar un archivo de pago">


      <form @submit.prevent="procesarArchivos">
      <div class="form-group">
        <label for="archivoControl">Elige el archivo de control</label>
        <input id="archivoControl" class="form-control" required type="file" v-on:change="seleccionarArchivoControl" name="control" accept=".csv,.txt"/>
      </div>
      <div class="form-group">
        <label for="archivoPago">Elige el archivo de pago</label>
        <input class="form-control" required type="file" id="archivoPago" v-on:change="seleccionarArchivoPago" name="pago" accept=".csv,.txt"/>
      </div>




      <h3>Archivo de control</h3>

    <b-table
      id="my-table"
      :items="items"
      :per-page="perPage"
      :current-page="currentPage"
      small
    ></b-table>
      
    <b-pagination
      v-model="currentPage"
      :total-rows="rows"
      :per-page="perPage"
      aria-controls="my-table"
    ></b-pagination>

    <p class="mt-3">Página: {{ currentPage }}</p>      


      <button class="btn btn-info" round>Procesar archivo</button>
      

      </form>
     </card>


  </div>
</template>
<script>




import api from '@/util/api.js';


export default {
  components: {
  },

  computed:{

    
      rows() {
        return this.items.length
    }

  },

  data() {
    return {
      

      model:{

        control:'',
        pago:'',
      },

      perPage: 50,
      currentPage: 1,
      items: []



    }
  },

  methods:{


    seleccionarArchivoControl(event){

      this.model.control = event.target.files[0];
    },

    seleccionarArchivoPago(event){

      this.model.pago = event.target.files[0];
    },

    procesarArchivos(){


      

      api.post('/uploader',this.createFormData(this.model)).then(response=>{


        this.items=response.data.items;

      });

    },


  }


};
</script>
<style></style>
